import React, { useEffect, useState } from "react";
import { useAuthState } from 'react-firebase-hooks/auth';
import { auth } from '../../components/firebase/firebaseConfig';
import { toast, ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import PDFViewer from '../../components/pdfRender/PDFViewer';
import PDFToolbar from '../../components/pdfRender/PDFToolbar';
import PDFReferences from '../../components/pdfRender/PDFReferences';
import LoadingIndicator from '../../components/pdfRender/LoadingIndicator';
import { PDFDocument } from "pdf-lib";
import { useNavigate } from 'react-router-dom';
import * as pdfjsLib from "pdfjs-dist";

const PDFRenderer = () => {
    const [open, setOpen] = useState(false); // Definimos el estado 'open'

    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);
  const [user] = useAuthState(auth);
  const [formData, setFormData] = useState({});
  const [images, setImages] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const calculados = (d) => {
    return {
      'Textfield-0 po ejemplo': 'dd', // Ejemplo de cálculo
    };
  };

  const pdfUrl = './AGR010  Pantallas térmicas en invernaderos.pdf';
  const Referencias = [
    { nombre: 'Sistema de Certificados de Ahorro Energético (CAE)', url: 'https://www.miteco.gob.es/es/energia/eficiencia/cae.html' },
  ];

  const handleDownload = async () => {
    if (user) {
      const modifiedPdfBytes = await fillPdfFields(pdfUrl);
      const blob = new Blob([modifiedPdfBytes], { type: 'application/pdf' });
      const link = document.createElement('a');
      link.href = URL.createObjectURL(blob);
      link.download = 'modified-pdf.pdf';
      link.click();
    } else {
      toast.error('Debe Iniciar sesión');
    }
  };

  const updatePdfWithNewValues = async () => {
    setLoading(true);
    try {
      const modifiedPdfBytes = await fillPdfFields(pdfUrl);
      await renderPdfAsImages(modifiedPdfBytes);
    } catch (err) {
      setError(`An error occurred while updating the PDF: ${err.message}`);
    } finally {
      setLoading(false);
    }
  };

  const fillPdfFields = async (pdfUrl) => {
    const existingPdfBytes = await fetch(pdfUrl).then((res) => res.arrayBuffer());
    const pdfDoc = await PDFDocument.load(existingPdfBytes);
    const form = pdfDoc.getForm();
    const updatedData = calculados(formData);
    Object.entries(updatedData).forEach(([nombre, valor]) => {
      const field = form.getField(nombre);
      field.setText(String(valor));
    });
    return await pdfDoc.save();
  };

  const renderPdfAsImages = async (modifiedPdfBytes) => {
    try {
      const pdf = await pdfjsLib.getDocument({ data: modifiedPdfBytes }).promise;
      const pages = [];
      for (let i = 1; i <= pdf.numPages; i++) {
        const page = await pdf.getPage(i);
        const viewport = page.getViewport({ scale: 2.5 });
        const canvas = document.createElement("canvas");
        const context = canvas.getContext("2d");
        canvas.width = viewport.width;
        canvas.height = viewport.height;
        await page.render({ canvasContext: context, viewport }).promise;
        pages.push(canvas.toDataURL("image/png"));
      }
      setImages(pages);
    } catch (err) {
      setError("Failed to render PDF.");
    }
  };

  useEffect(() => {
    const initialData = calculados(formData);
    setFormData(initialData);
    updatePdfWithNewValues();
  }, []);

  return (
    <div>
      <PDFToolbar 
        onSettingsClick={() => setOpen(true)} 
        onDownloadClick={handleDownload} 
        isAuthenticated={!!user} 
      />

      {loading && <LoadingIndicator />}
      {error && <p style={{ color: "red", textAlign: "center" }}>{error}</p>}
      <PDFViewer images={images} onImageClick={() => setOpen(true)} />
      <PDFReferences references={Referencias} />
      <ToastContainer />
    </div>
  );
};

export default PDFRenderer;
